import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_SET_ROW_HEIGHT = Symbol('TABLE_SET_ROW_HEIGHT');

export function setTableRowHeight(table, newHeight: number) {
  return {
    type: TABLE_SET_ROW_HEIGHT,
    payload: {
      node: table,
      newHeight,
    },
  };
}

interface TableSetTableRowHeightPayload {
  node: Table;
  newHeight: number;
}

interface TableSetTableRowHeightAction {
  type: typeof TABLE_SET_ROW_HEIGHT;
  payload: TableSetTableRowHeightPayload;
}

export function isTableSetTableRowHeightAction(
  action: Action,
): action is TableSetTableRowHeightAction {
  return action.type === TABLE_SET_ROW_HEIGHT;
}
